// Hydration-safe date formatter - returns empty string on server, formatted date on client
const formatDateSafe = (dateString: string, options?: Intl.DateTimeFormatOptions): string => {
  if (typeof window === 'undefined') return dateString // Return raw string on server
  try {
    return new Date(dateString).toLocaleDateString('en-US', options || { month: 'short', day: 'numeric' })
  } catch {
    return dateString
  }
}

