// Helper Functions
const formatCurrency = (amount: number) => {
  return new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  }).format(amount)
}

const getRiskColor = (risk: string) => {
  switch (risk) {
    case 'low': return 'bg-emerald-100 text-emerald-700'
    case 'medium': return 'bg-amber-100 text-amber-700'
    case 'high': return 'bg-red-100 text-red-700'
    default: return 'bg-gray-100 text-gray-700'
  }
}

const getScoreColor = (score: number) => {
  if (score >= 80) return 'text-emerald-600'
  if (score >= 60) return 'text-amber-600'
  return 'text-red-600'
}

const getPriorityColor = (priority: string) => {
  switch (priority) {
    case 'high': return 'bg-red-100 text-red-700 border-red-200'
    case 'medium': return 'bg-amber-100 text-amber-700 border-amber-200'
    case 'low': return 'bg-blue-100 text-blue-700 border-blue-200'
    default: return 'bg-gray-100 text-gray-700 border-gray-200'
  }
}

// Main Component
export default function AIRevenueIntelligencePage() {
  const [activeTab, setActiveTab] = useState('dashboard')
  const [selectedDeal, setSelectedDeal] = useState<Deal | null>(null)
  const [showDealModal, setShowDealModal] = useState(false)
  const [isClient, setIsClient] = useState(false)

  // Hydration fix - only render dynamic content after mount
  useEffect(() => {
    setIsClient(true)
  }, [])

  // Calculate dashboard metrics
  const metrics = useMemo(() => {
    const totalPipeline = mockDeals.reduce((sum, d) => sum + d.value, 0)
    const weightedPipeline = mockDeals.reduce((sum, d) => sum + (d.value * d.probability / 100), 0)
    const avgAiScore = mockDeals.reduce((sum, d) => sum + d.aiScore, 0) / mockDeals.length
    const atRiskDeals = mockDeals.filter(d => d.aiRisk === 'high').length
    const closingThisMonth = mockDeals.filter(d => new Date(d.closeDate) <= new Date('2025-01-31'))
    const commitedRevenue = closingThisMonth.filter(d => d.aiScore >= 80).reduce((sum, d) => sum + d.value, 0)
    
    return {
      totalPipeline,
      weightedPipeline,
      avgAiScore: Math.round(avgAiScore),
      atRiskDeals,
      commitedRevenue,
      dealCount: mockDeals.length,
      highScoreDeals: mockDeals.filter(d => d.aiScore >= 80).length,
    }
  }, [])

  const handleViewDeal = (deal: Deal) => {
    setSelectedDeal(deal)
    setShowDealModal(true)
  }

  return (
    <div className="space-y-6 -m-4 sm:-m-6 lg:-m-8">
      {/* Page Header */}
      <div className="flex-none bg-white border-b border-gray-200 px-4 sm:px-6 lg:px-8 pt-4 sm:pt-6 lg:pt-8 pb-0">
        <PageHeader
          title="AI Revenue Intelligence"
          description="Predictive insights, deal scoring, and revenue forecasting powered by AI"
          actions={
            <div className="flex items-center gap-3">
              <button className="px-4 py-2 text-gray-600 border border-gray-300 rounded-lg hover:bg-gray-50 text-sm flex items-center gap-2">
                <span>üìä</span>
                Export Report
              </button>
              <button className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 text-sm flex items-center gap-2">
                <span>üîÑ</span>
                Refresh Insights
              </button>
            </div>
          }
        />

        {/* Tab Navigation */}
        <nav className="-mb-px flex space-x-6 overflow-x-auto" aria-label="Tabs">
          {AI_REVENUE_TABS.map((tab) => {
            const isActive = tab.id === activeTab
            return (
              <button
                key={tab.id}
                onClick={() => setActiveTab(tab.id)}
                className={`whitespace-nowrap border-b-2 py-4 px-1 text-sm font-medium transition-colors ${
                  isActive
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-gray-500 hover:border-gray-300 hover:text-gray-700'
                }`}
              >
                {tab.label}
              </button>
            )
          })}
        </nav>
      </div>

      {/* Tab Content */}
      <div className="px-4 sm:px-6 lg:px-8 pb-4 sm:pb-6 lg:pb-8">
        {activeTab === 'dashboard' && (
          <DashboardTab
            metrics={metrics}
            deals={mockDeals}
            forecasts={mockForecasts}
            recommendations={mockRecommendations}
            onViewDeal={handleViewDeal}
          />
        )}
        {activeTab === 'deal-insights' && (
          <DealInsightsTab deals={mockDeals} onViewDeal={handleViewDeal} />
        )}
        {activeTab === 'forecasting' && (
          <ForecastingTab forecasts={mockForecasts} />
        )}
        {activeTab === 'pipeline-health' && (
          <PipelineHealthTab deals={mockDeals} />
        )}
        {activeTab === 'recommendations' && (
          <RecommendationsTab recommendations={mockRecommendations} />
        )}
        {activeTab === 'win-loss' && (
          <WinLossTab />
        )}
        {activeTab === 'settings' && (
          <SettingsTab />
        )}
      </div>

      {/* Deal Detail Modal */}
      {showDealModal && selectedDeal && (
        <DealDetailModal
          deal={selectedDeal}
          onClose={() => {
            setShowDealModal(false)
            setSelectedDeal(null)
          }}
        />
      )}
    </div>
  )
}

