// ==============================================
// PIPELINE HEALTH TAB
// ==============================================
function PipelineHealthTab({ deals }: { deals: Deal[] }) {
  const stageData = useMemo(() => {
    const stages = ['Qualification', 'Discovery', 'Proposal', 'Negotiation', 'Closed Won']
    return stages.map(stage => {
      const stageDeals = deals.filter(d => d.stage === stage)
      return {
        stage,
        count: stageDeals.length,
        value: stageDeals.reduce((sum, d) => sum + d.value, 0),
        avgDaysInStage: stageDeals.length > 0
          ? Math.round(stageDeals.reduce((sum, d) => sum + d.daysInStage, 0) / stageDeals.length)
          : 0,
        avgScore: stageDeals.length > 0
          ? Math.round(stageDeals.reduce((sum, d) => sum + d.aiScore, 0) / stageDeals.length)
          : 0,
      }
    })
  }, [deals])

  const healthMetrics = useMemo(() => {
    const avgEngagement = Math.round(deals.reduce((sum, d) => sum + d.engagementScore, 0) / deals.length)
    const stuckDeals = deals.filter(d => d.daysInStage > 14).length
    const healthScore = Math.round(
      (avgEngagement * 0.4) +
      ((100 - (stuckDeals / deals.length) * 100) * 0.3) +
      (deals.reduce((sum, d) => sum + d.aiScore, 0) / deals.length * 0.3)
    )
    return { avgEngagement, stuckDeals, healthScore }
  }, [deals])

  return (
    <div className="space-y-6">
      {/* Health Score Card */}
      <div className="bg-gradient-to-r from-emerald-500 to-teal-600 rounded-xl p-6 text-white">
        <div className="flex items-center justify-between">
          <div>
            <h3 className="text-lg font-semibold mb-2">Pipeline Health Score</h3>
            <div className="text-5xl font-bold mb-2">{healthMetrics.healthScore}</div>
            <div className="text-emerald-100">Out of 100 ‚Ä¢ Good Health</div>
          </div>
          <div className="text-8xl opacity-20">üí™</div>
        </div>
      </div>

      {/* Health Metrics */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <div className="bg-white rounded-xl border border-gray-200 p-6">
          <div className="text-3xl mb-2">üìä</div>
          <div className="text-2xl font-bold text-gray-900">{healthMetrics.avgEngagement}%</div>
          <div className="text-sm text-gray-500">Avg. Engagement Score</div>
          <div className="text-xs text-emerald-600 mt-1">‚Üë 5% vs last month</div>
        </div>
        <div className="bg-white rounded-xl border border-gray-200 p-6">
          <div className="text-3xl mb-2">‚è∞</div>
          <div className="text-2xl font-bold text-amber-600">{healthMetrics.stuckDeals}</div>
          <div className="text-sm text-gray-500">Deals Stuck 14+ Days</div>
          <div className="text-xs text-gray-400 mt-1">Needs attention</div>
        </div>
        <div className="bg-white rounded-xl border border-gray-200 p-6">
          <div className="text-3xl mb-2">üîÑ</div>
          <div className="text-2xl font-bold text-gray-900">18 days</div>
          <div className="text-sm text-gray-500">Avg. Sales Cycle</div>
          <div className="text-xs text-emerald-600 mt-1">‚Üì 3 days vs benchmark</div>
        </div>
      </div>

      {/* Stage Analysis */}
      <div className="bg-white rounded-xl border border-gray-200">
        <div className="px-6 py-4 border-b border-gray-100">
          <h3 className="font-semibold text-gray-900">Stage-by-Stage Analysis</h3>
        </div>
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="border-b border-gray-200">
                <th className="text-left py-3 px-6 text-sm font-medium text-gray-500">Stage</th>
                <th className="text-center py-3 px-6 text-sm font-medium text-gray-500">Deals</th>
                <th className="text-right py-3 px-6 text-sm font-medium text-gray-500">Value</th>
                <th className="text-center py-3 px-6 text-sm font-medium text-gray-500">Avg. Days</th>
                <th className="text-center py-3 px-6 text-sm font-medium text-gray-500">Avg. AI Score</th>
                <th className="text-center py-3 px-6 text-sm font-medium text-gray-500">Health</th>
              </tr>
            </thead>
            <tbody>
              {stageData.map((stage) => (
                <tr key={stage.stage} className="border-b border-gray-100 hover:bg-gray-50">
                  <td className="py-4 px-6 font-medium text-gray-900">{stage.stage}</td>
                  <td className="py-4 px-6 text-center">{stage.count}</td>
                  <td className="py-4 px-6 text-right font-medium">{formatCurrency(stage.value)}</td>
                  <td className="py-4 px-6 text-center">
                    <span className={`${stage.avgDaysInStage > 10 ? 'text-amber-600' : 'text-gray-600'}`}>
                      {stage.avgDaysInStage} days
                    </span>
                  </td>
                  <td className="py-4 px-6 text-center">
                    <span className={getScoreColor(stage.avgScore)}>{stage.avgScore}%</span>
                  </td>
                  <td className="py-4 px-6 text-center">
                    <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                      stage.avgScore >= 70 ? 'bg-emerald-100 text-emerald-700' :
                      stage.avgScore >= 50 ? 'bg-amber-100 text-amber-700' :
                      'bg-red-100 text-red-700'
                    }`}>
                      {stage.avgScore >= 70 ? 'Healthy' : stage.avgScore >= 50 ? 'Monitor' : 'At Risk'}
                    </span>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  )
}

// ==============================================
// RECOMMENDATIONS TAB
// ==============================================
function RecommendationsTab({ recommendations }: { recommendations: Recommendation[] }) {
  const [filterType, setFilterType] = useState<string>('')
  const [filterPriority, setFilterPriority] = useState<string>('')

  const filteredRecs = useMemo(() => {
    return recommendations.filter(r => {
      if (filterType && r.type !== filterType) return false
      if (filterPriority && r.priority !== filterPriority) return false
      return true
    })
  }, [recommendations, filterType, filterPriority])

  const typeIcons: Record<string, string> = {
    deal: 'üíº',
    pipeline: 'üìä',
    activity: 'üìß',
    coaching: 'üéì',
  }

  return (
    <div className="space-y-6">
      {/* Filters */}
      <div className="flex items-center gap-3">
        <select
          value={filterType}
          onChange={(e) => setFilterType(e.target.value)}
          className="px-4 py-2 border border-gray-300 rounded-lg text-sm"
        >
          <option value="">All Types</option>
          <option value="deal">Deal</option>
          <option value="pipeline">Pipeline</option>
          <option value="activity">Activity</option>
          <option value="coaching">Coaching</option>
        </select>
        <select
          value={filterPriority}
          onChange={(e) => setFilterPriority(e.target.value)}
          className="px-4 py-2 border border-gray-300 rounded-lg text-sm"
        >
          <option value="">All Priorities</option>
          <option value="high">High Priority</option>
          <option value="medium">Medium Priority</option>
          <option value="low">Low Priority</option>
        </select>
      </div>

      {/* Recommendations List */}
      <div className="space-y-4">
        {filteredRecs.map((rec) => (
          <div
            key={rec.id}
            className={`bg-white rounded-xl border-2 p-6 ${
              rec.priority === 'high' ? 'border-red-200' :
              rec.priority === 'medium' ? 'border-amber-200' :
              'border-gray-200'
            }`}
          >
            <div className="flex items-start justify-between mb-4">
              <div className="flex items-start gap-4">
                <div className="text-3xl">{typeIcons[rec.type]}</div>
                <div>
                  <div className="flex items-center gap-2 mb-1">
                    <h4 className="font-semibold text-gray-900">{rec.title}</h4>
                    <span className={`px-2 py-0.5 text-xs font-medium rounded-full ${getPriorityColor(rec.priority)}`}>
                      {rec.priority}
                    </span>
                  </div>
                  <p className="text-gray-600 mb-2">{rec.description}</p>
                  {rec.dealName && (
                    <p className="text-sm text-gray-500">
                      Related Deal: <span className="font-medium text-blue-600">{rec.dealName}</span>
                    </p>
                  )}
                </div>
              </div>
            </div>
            <div className="flex items-center justify-between pt-4 border-t border-gray-100">
              <div className="text-sm">
                <span className="text-gray-500">Potential Impact: </span>
                <span className="font-medium text-emerald-600">{rec.impact}</span>
              </div>
              <button className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 text-sm font-medium">
                {rec.actionLabel}
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}

